import { _decorator, Component, Node,instantiate, Button, UITransform, UI, Enum ,Label} from 'cc';
import { AS } from "db://as_framework/scripts//ASComponent";
import { UIAnimationLibrary } from "db://as_framework/scripts/UIAnimLibrary";
import { ReplicatedButton } from "db://as_framework/scripts/ReplicatedButton";
import { toUSVString } from 'util';
import { threadId } from 'worker_threads';
const { ccclass, property, requireComponent } = _decorator;

enum AnswerType {
    SINGLE_CORRECT,
    MULTIPLE_CORRECT
}

@ccclass('MCQ')
export class MCQ extends AS(Component) {

    //@property({type: Enum(AnswerType)}) private answerType = AnswerType.SINGLE_CORRECT;

   // @property({visible: function (this: MCQ) {return this.answerType === AnswerType.SINGLE_CORRECT}}) private x;
    private _selection =[ ] ;
    @property private answer = [-1,1,1,-1];
    @property({type: String})
    private optionLabel: string[] = [];
    awake() {
        const insertButtonsComp = this.node.children;
        insertButtonsComp.forEach((child, i) => {
                child.addComponent(Button);
                child.addComponent(ReplicatedButton);
        })
        const choiceButtons = this.node.getComponentsInChildren(Button);
        choiceButtons.forEach((btn,i) => {
            if(btn.node.name!="check")this._selection.push(-1);
            console.log("running");
            btn.node.on(Button.EventType.CLICK,async()=>{
                if(btn.node.name=="check") {
                    this.checkSelection();
                    return;
                }
                if(this._selection[i]==-1) {
                    btn.node.children[0].active =true; 
                    this._selection[i]=1;
                }
                else{
                    btn.node.children[0].active=false;
                    this._selection[i]=-1;}
            })
        });    
        this.lableAssigner();
    }
    checkSelection()
    {
        if(this._selection==this.answer){
            this.checkFeedback(1)
        }else{
            this.checkFeedback(2)
        }
    }
    lableAssigner()
    {
     const mcqLable=  this.node.getComponentsInChildren(Label);  
     mcqLable.forEach((btnLable,i) => {
      if(this.optionLabel.length<=i){
        btnLable.node.parent.active=false;
      }
      else
      btnLable.string=this.optionLabel[i];
     });
    }
    checkFeedback(statusIndex)
    {
        const btn= this.node.children;
        for(let i=0;i<this._selection.length;i++){
            //btn[i].children[statusIndex].addComponent(UIAnim);
            if(this._selection[i]==1){
                let animation=btn[i].children[statusIndex]
                UIAnimationLibrary.flash(btn[i].children[statusIndex],2);
            }
        }
    }
}

